
@import url('../assets/markdown-github.css');
.article span:first-child {
    margin-left: 0;
}
.article {
    width: 60%;
    background: lightgrey;
    border: 1px solid lightgrey;
    padding: 2rem;
    background: #F9FAFC;
}
.article span,
.article a {
    font-size: 1rem;
    color: black;
    margin: 1rem 1rem;
    text-decoration: none;
}
.article .top-span {
    color: grey;
    margin: 0 0.5rem;
}
#content {
    margin: 2rem auto 2rem auto;
    padding: 2rem 1rem 2rem 1rem;
    border: 1px solid #ddd;
    line-height: 1.6;
    padding-bottom: 1rem;
    background: #EFF2F7;
}
#reply {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
#reply img {
    width: 5rem;
    height: 5rem;
    margin-right: 1rem;
}
.reply > div {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    margin-left: 1rem;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.reply .reply-name {
    color: grey;
    font-size: 1.5rem;
}
.reply-top {
    width: 100%;
}
.reply-ups {
    float: right;
}



